import {Component, OnInit, ViewChild} from '@angular/core';
import {icons} from '../../../../auth/helpers';
import {ColumnMode, DatatableComponent} from '@swimlane/ngx-datatable';
import {NgbModal} from '@ng-bootstrap/ng-bootstrap';
import {RolesEditComponent} from '../roles-edit/roles-edit.component';
import {RolesAddComponent} from '../roles-add/roles-add.component';
import {Role, RolesService} from '../roles.service';
import {UserService} from '../../../../auth/service';
import {User1} from '../../../../auth/models';
import {UserLimitsComponent} from "../../user/user-limits/user-limits.component";
import {UserEditComponent} from "../../user/user-edit/user-edit.component";
import {ToastrService} from "ngx-toastr";
import Swal from "sweetalert2";

@Component({
    selector: 'app-roles-list',
    templateUrl: './roles-list.component.html',
    styleUrls: ['./roles-list.component.scss']
})
export class RolesListComponent implements OnInit {
    perPage = 10;
    q: string;

    constructor(private modalService: NgbModal,
                private role: RolesService,
                private user: UserService,
                private toastr: ToastrService
    ) {
    }

    @ViewChild(DatatableComponent) table: DatatableComponent;

    roles: Role[];
    roles2: Role[];
    selectAll = false;
    loading = true;
    loadingUsers = true;
    deletingUsers={}
    loadingRoles = true;
    withUsers = false


    protected readonly icons = icons;
    users: User1[];
    originalUsers: User1[];
    protected readonly ColumnMode = ColumnMode;

    ngOnInit() {
        this.loadRoles()
        this.loadUsers()
    }
    loadRoles(){
        this.loadingRoles=true
        this.role.get().then(res => {
            this.roles = res.data;
            this.roles2 = this.roles.filter(r => r.user_count)
            this.loadingRoles = false;
        });
    }
    loadUsers(){
        this.loadingUsers=true
        this.user.get().then(res => {
            this.originalUsers = this.users = res.data;
            this.loadingUsers = false;
        });
    }

    public selectedOption = 5;

    add() {
        let modal = this.modalService.open(RolesAddComponent, {
            centered: true,
            size: 'xl'
        });
        modal.componentInstance.role = {};
    }

    edit(role) {
        let modal = this.modalService.open(RolesEditComponent, {
            centered: true,
            size: 'xl'
        });
        modal.componentInstance.role = role;
        modal.result?.then((r) => {
            if (r === 'reload') {
                this.loadRoles();
            }
        });
    }

    editUser(user) {
        console.log('row: ', user);
        const modal = this.modalService.open(UserEditComponent, {
            size: 'lg'
        });
        modal.componentInstance.oldUser = user;
    }

    openLimits(u: User1) {
        let modal = this.modalService.open(UserLimitsComponent, {
            centered: true,
            size: 'lg'
        });
        modal.componentInstance.userId = u.id
    }

    delete(user) {
        Swal.fire({
            title: 'Are you sure?',
            text: "You won't be able to revert this!",
            icon: 'warning',
            showCancelButton: true,
            confirmButtonColor: '#7367F0',
            cancelButtonColor: '#E42728',
            confirmButtonText: 'Yes, Remove this user',
            customClass: {
                confirmButton: 'btn btn-primary',
                cancelButton: 'btn btn-danger ml-1'
            }
        }).then((result) => {
            if (result.value) {
                this.deletingUsers[user.id]=true
                this.user.delete(user.id).then(res => {
                    this.loadUsers()
                    Swal.fire({
                        icon: 'success',
                        title: 'Removed!',
                        text: 'This user Has Been Removed Successfully!',
                        customClass: {
                            confirmButton: 'btn btn-success'
                        }
                    })})
            }
        }).catch(error=>{
            Swal.fire({
                    icon: 'error',
                    title: 'Oops',
                    text: 'Something went wrong'
                }
            ).finally(()=>{
                delete(this.deletingUsers[user.id])
            })
        });
    }

    search(q: string) {
        q = q.toLowerCase();
        if (!q) {
            this.users = this.originalUsers;
            return;
        }
        this.users = this.originalUsers.filter(u => u.roles.some(r => r.toLowerCase().includes(q)) ||
            ['email', 'first_name', 'last_name', 'full_name'].some(k => u[k]?.toLowerCase()?.includes(q))
        );
        this.table.offset = 0;
    }
}
