import {Component, OnInit} from '@angular/core';
import {NgbActiveModal} from '@ng-bootstrap/ng-bootstrap';
import {ToastrService} from 'ngx-toastr';
import {RolesService, RoleTemplate} from '../roles.service';

@Component({
    selector: 'app-roles-add',
    templateUrl: './roles-add.component.html',
    styleUrls: ['./roles-add.component.scss']
})
export class RolesAddComponent implements OnInit {
    constructor(public activeModal: NgbActiveModal,
                private toastr: ToastrService,
                private roles: RolesService
    ) {
    }

    role: { name: string, permissions: string[] } = {} as any;

    permissions: RoleTemplate;

    selectAll = false;
    errors: {
        name?: boolean,
        noperm?: boolean
    } = {};
    loading = true;
    loadingSave = true;
    saved: { [perm: string]: boolean } = {};

    ngOnInit() {
        this.roles.getRoleTemplate().then(res => {
            this.permissions = res.data.roles_to_display;
            this.loading = false;
            console.log('permissions : ', this.permissions);
        });
    }

    save() {
        this.errors = {};
        this.role.permissions = Object.keys(this.saved).filter(k => this.saved[k]);
        if (!(this.role.name?.length > 2)) {
            this.errors.name = true;
            return;
        } else if (this.role.permissions.length === 0) {
            this.errors.noperm = true;
            return;
        }
        this.loadingSave = true;
        console.log('this.saved role', this.role);
        this.roles.add({role_name: this.role.name, role_permissions: this.role.permissions}).then(res => {
            if (!res.success) {
                throw new Error();
            }
            this.toastr.success('Role Saved Successfully');
            this.loadingSave = false;
            this.activeModal.dismiss();
        }).catch(err => {
            this.toastr.success('Something went wrong.');
        }).finally(() => {
            this.loadingSave = false;
        });
    }

}
